<template>
    <div class="tabs"> 
        <slot></slot>
    </div>
</template>

<script>
    import Vue from 'vue'
    export default {
        name:'WTabs',
        props:{
            selected:{
                type:String,
                required:true
            },
            direction:{
                type:String,
                default: 'horizontal',
                validator(value) {
                    return ['horizontal','vertical'].indexOf(value) >= 0 
                }
            }
        },
        data(){
            return {
                //$emit $on $off
                eventBus:new Vue()
            }
        },
        provide(){
            //provide inject
            return {
                eventBus:this.eventBus
            }
        },
        mounted(){
            if(this.$children.length === 0){
                console.warning('tabstabs-headtabs-nav')
            }
            //selected eventBus
            // 
            //item
            //Line
            this.$children.forEach((vm)=>{
                if(vm.$options.name === 'WTabsHead') {
                    vm.$children.forEach((childVm) => {
                        if(childVm.$options.name === 'WTabsItem' 
                        && childVm.name === this.selected){
                            this.eventBus.$emit('update:selected', this.selected, childVm)
                        }
                    })
                }
            })
        }
    }
</script>

